<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process112"></a>

<p><br></p>
<p><span class="rvts17">彻底搜查（</span><span class="rvts50">The Shakedowns</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">起始坐标：</span>
<br><span class="rvts20">X:209,Y:179</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：埃罗迪（</span><span class="rvts20">Elodi</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">调停争斗</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1014.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在进入欢乐堡大门后，第一件闹心事就是看到吵架的一堆人。吵架的原因主要是因为保护费的问题。当所有流浪者都被流放到这个穷僻的小岛后，就会出现所谓的老大和所谓的需要缴纳</span><span class="rvts20"></span><span class="rvts21">保护费</span><span class="rvts20"></span><span class="rvts21">的弱者。</span>
<br><span class="rvts21">一定要注意对话的选择立场问题，记得一定要站在弱势群体精灵</span><span class="rvts20">Elodi</span><span class="rvts21">的那一派，因为伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹是站在弱者这一边的。</span>
<br><span class="rvts21">如果选择站在恶人一边，那么伊凡会与开战。如果站在</span><span class="rvts20">Elodi</span><span class="rvts21">的一边，那么无论是否压制住这帮地痞还是与他们开战，都会比较容易获胜，并获得任务的进展。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1015.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">当处理完这起吵架事故后，跟随</span><span class="rvts20">Elodi</span><span class="rvts21">会进入贫民躲避的洞穴，来到这里与她对话，便可完成任务。</span>
<br><span class="rvts21">任务奖励一个可怕的头颅，精灵族吃后会获得</span><span class="rvts20"></span><span class="rvts21">急救</span><span class="rvts20"></span><span class="rvts21">技能。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



